'use client';

import { useEffect, useState } from 'react';

export default function TestFetchPage() {
  const [result, setResult] = useState<string>('Loading...');

  useEffect(() => {
    const testFetch = async () => {
      try {
        const response = await fetch('/api/prikbord');
        const data = await response.json();
        setResult(`Success: ${JSON.stringify(data)}`);
      } catch (error) {
        setResult(`Error: ${error instanceof Error ? error.message : 'Unknown error'}`);
      }
    };

    testFetch();
  }, []);

  return (
    <div className="p-8">
      <h1 className="text-2xl font-bold mb-4">Fetch Test Page</h1>
      <pre className="bg-gray-100 p-4 rounded">{result}</pre>
    </div>
  );
}
